<template>
  <div id="app">
    <!--  -->
    <div v-if="currentPage === 'login' || currentPage === 'register' || currentPage === 'reset'">
      <LoginPage v-if="currentPage === 'login'" 
                @goto-register="goToRegister" 
                @goto-reset="goToReset" 
                @login-success="handleLoginSuccess" />
      <RegisterPage v-else-if="currentPage === 'register'" @goto-login="goToLogin" />
      <ResetPasswordPage v-else-if="currentPage === 'reset'" @goto-login="goToLogin" />
    </div>
    
    <!--  -->
    <Navigation 
      v-else 
      :current-module="currentModule" 
      :username="username" 
      :user-role="userRole"
      @navigate="handleNavigate" 
      @logout="handleLogout"
    >
      <!--  -->
      <ResourceRequest 
        v-if="currentModule === 'resource' || currentModule === 'allRequests'"
        :user-role="userRole"
      />
      <ResourceMonitoring v-else-if="currentModule === 'monitor'" :user-role="userRole" />
      <ServerManagement v-else-if="currentModule === 'servers'" :user-role="userRole" />
      <AdminRequests v-else-if="currentModule === 'adminRequests'" :user-role="userRole" />
    </Navigation>
  </div>
</template>

<script>
// 
import LoginPage from './views/Auth/Login.vue'
import RegisterPage from './views/Auth/Register.vue'
import ResetPasswordPage from './views/Auth/ResetPassword.vue'

// 
import Navigation from './components/Navigation.vue'
import ServerManagement from './views/Admin/ServerManagement.vue'
import ResourceRequest from './views/Admin/ResourceRequest.vue'
import ResourceMonitoring from './views/Admin/ResourceMonitoring.vue'
import AdminRequests from './views/Admin/AdminRequests.vue'

export default {
  name: 'App',
  components: {
    // 
    LoginPage,
    RegisterPage,
    ResetPasswordPage,
    
    // 
    Navigation,
    ResourceRequest,
    ResourceMonitoring,
    ServerManagement,
    AdminRequests
  },
  data() {
    return {
      currentPage: 'login',  // 
      currentModule: 'resource',  // 
      username: '',  // 
      userRole: ''  // : 'user'  'admin'
    }
  },
  methods: {
    // 
    goToRegister() {
      this.currentPage = 'register'
    },
    
    goToReset() {
      this.currentPage = 'reset'
    },
    
    goToLogin() {
      this.currentPage = 'login'
    },
    
    // 
    handleLoginSuccess(userInfo) {
      this.username = userInfo.username || ''
      this.userRole = userInfo.role || 'user'
      
      // 
      this.currentPage = 'main'
      this.currentModule = this.userRole === 'admin' ? 'monitor' : 'resource'
    },
    
    // 
    handleNavigate(module) {
      this.currentModule = module
    },
    
    // 
    handleLogout() {
      this.currentPage = 'login'
      console.log('')
    }
  }
}
</script>

<style>
/*  */
@import './assets/styles.css';

#app {
  width: 100%;
  height: 100vh;
}
</style>