<template>
  <div class="request-form-container">
    <h2></h2>
    <form class="request-form" @submit.prevent="handleSubmitRequest">
      <!--  -->
      <div class="form-section">
        <h3></h3>
        <div class="form-row">
          <div class="form-group">
            <label for="resourceType"> <span class="required">*</span></label>
            <select v-model="formData.resourceType" id="resourceType" class="form-select">
              <option value=""></option>
              <option value="server"></option>
              <option value="gpu"></option>
              <option value="storage"></option>
              <option value="network"></option>
              <option value="software"></option>
              <option value="other"></option>
            </select>
          </div>
          <div class="form-group">
            <label for="requestTitle"> <span class="required">*</span></label>
            <input v-model="formData.requestTitle" type="text" id="requestTitle" class="form-input" placeholder="">
          </div>
          <div class="form-group">
            <label for="urgencyLevel"></label>
            <select v-model="formData.urgencyLevel" id="urgencyLevel" class="form-select">
              <option value="normal"></option>
              <option value="urgent"></option>
              <option value="veryUrgent"></option>
            </select>
          </div>
          <div class="form-group">
            <label for="expectedDate"></label>
            <input v-model="formData.expectedDate" type="date" id="expectedDate" class="form-input">
          </div>
        </div>
      </div>

      <!--  -->
      <div class="form-section">
        <h3></h3>
        <div class="form-row">
          <div class="form-group">
            <label for="serverId"> <span class="required">*</span></label>
            <select v-model="formData.serverId" id="serverId" class="form-select" @change="onServerChange">
              <option value=""></option>
              <option v-for="server in availableServers" :key="server.id" :value="server.id">
                {{ server.name }} ({{ server.ip }}) - CPU: {{ server.cpuUsage }}%, Memory: {{ server.memoryUsage }}%
              </option>
            </select>
          </div>
          <div class="form-group">
            <label for="gpuIndex"> <span class="required">*</span></label>
            <input v-model="formData.gpuIndex" type="number" id="gpuIndex" class="form-input" placeholder="1" min="1" :max="availableGpuCount">
          </div>
          <div class="form-group">
            <label for="gpuCount"> <span class="required">*</span></label>
            <input v-model="formData.gpuCount" type="number" id="gpuCount" class="form-input" placeholder="1" min="1" :max="availableGpuCount">
          </div>
        </div>
        <div class="form-row">
          <div class="form-group full-width">
            <label for="resourceDescription"></label>
            <textarea v-model="formData.resourceDescription" id="resourceDescription" class="form-textarea" placeholder="..."></textarea>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="form-section">
        <h3></h3>
        <div class="form-row">
          <div class="form-group">
            <label for="projectContributionScore"> (1-10) <span class="required">*</span></label>
            <input v-model="formData.projectContributionScore" type="number" id="projectContributionScore" class="form-input" min="1" max="10">
          </div>
          <div class="form-group">
            <label for="researchAchievementScore"> (1-10) <span class="required">*</span></label>
            <input v-model="formData.researchAchievementScore" type="number" id="researchAchievementScore" class="form-input" min="1" max="10">
          </div>
          <div class="form-group">
            <label for="resourceUsageEfficiencyScore"> (1-10) <span class="required">*</span></label>
            <input v-model="formData.resourceUsageEfficiencyScore" type="number" id="resourceUsageEfficiencyScore" class="form-input" min="1" max="10">
          </div>
          <div class="form-group">
            <label for="fairnessScore"> (1-10) <span class="required">*</span></label>
            <input v-model="formData.fairnessScore" type="number" id="fairnessScore" class="form-input" min="1" max="10">
          </div>
        </div>
        <div v-if="comprehensiveScore > 0" class="form-row">
          <div class="form-group">
            <label></label>
            <div class="score-display">
              {{ comprehensiveScore.toFixed(1) }}/10
            </div>
          </div>
          <div class="form-group">
            <label></label>
            <div class="score-display">
              {{ basicMatchScore.toFixed(1) }}/10
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="form-section">
        <h3></h3>
        <div class="form-row">
          <div class="form-group">
            <label for="contactPerson"> <span class="required">*</span></label>
            <input v-model="formData.contactPerson" type="text" id="contactPerson" class="form-input" placeholder="">
          </div>
          <div class="form-group">
            <label for="contactPhone"> <span class="required">*</span></label>
            <input v-model="formData.contactPhone" type="tel" id="contactPhone" class="form-input" placeholder="">
          </div>
          <div class="form-group">
            <label for="contactEmail"> <span class="required">*</span></label>
            <input v-model="formData.contactEmail" type="email" id="contactEmail" class="form-input" placeholder="">
          </div>
        </div>
      </div>

      <!--  -->
      <div class="form-actions">
        <button type="button" class="cancel-button" @click="resetForm"></button>
        <button type="submit" class="submit-button" :disabled="isSubmitting">
          {{ isSubmitting ? '...' : '' }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'ResourceRequestForm',
  props: {
    availableServers: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      isSubmitting: false,
      formData: {
        resourceType: '',
        requestTitle: '',
        urgencyLevel: 'normal',
        expectedDate: '',
        cpuCount: '',
        memorySize: '',
        storageSize: '',
        // GPU
        serverId: '',
        gpuIndex: '',
        gpuCount: 1,
        resourceDescription: '',
        // 
        projectContributionScore: '',
        researchAchievementScore: '',
        resourceUsageEfficiencyScore: '',
        fairnessScore: '',
        contactPerson: '',
        contactPhone: '',
        contactEmail: ''
      }
    }
  },
  methods: {
    // 
    async handleSubmitRequest() {
      try {
        // 
        if (!this.validateForm()) {
          return
        }
        
        this.isSubmitting = true
        // API
        await new Promise(resolve => setTimeout(resolve, 1500))
        
        // 
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        
        // (40%) + (30%) + (30%)
        const totalScore = projectContributionScore * 0.4 + researchAchievementScore * 0.3 + resourceUsageEfficiencyScore * 0.3
        
        // 
        const server = this.selectedServer
        
        // 
        alert(`\nIDREQ${Date.now().toString().slice(-8)}\n${totalScore.toFixed(1)}\nGPU\n- ${server.name} (${server.ip})\n- #${this.formData.gpuIndex}  #${parseInt(this.formData.gpuIndex) + parseInt(this.formData.gpuCount) - 1}\n- ${this.formData.gpuCount}`)
        this.resetForm()
      } catch (error) {
        console.error('', error)
        alert('')
      } finally {
        this.isSubmitting = false
      }
    },
    
    // 
    validateForm() {
      // 
      if (!this.formData.resourceType || !this.formData.requestTitle) {
        alert('')
        return false
      }
      
      // GPU
      if (!this.formData.serverId || !this.formData.gpuIndex || !this.formData.gpuCount) {
        alert('GPU')
        return false
      }
      
      // 1-10
      const scores = [
        this.formData.projectContributionScore,
        this.formData.researchAchievementScore,
        this.formData.resourceUsageEfficiencyScore,
        this.formData.fairnessScore
      ]
      
      for (const score of scores) {
        const numScore = parseInt(score)
        if (isNaN(numScore) || numScore < 1 || numScore > 10) {
          alert('1-10')
          return false
        }
      }
    
      // 
      if (!this.formData.contactPerson || !this.formData.contactPhone || !this.formData.contactEmail) {
        alert('')
        return false
      }
      
      return true
    },
    
    // 
    resetForm() {
      this.formData = {
        resourceType: '',
        requestTitle: '',
        urgencyLevel: 'normal',
        expectedDate: '',
        cpuCount: '',
        memorySize: '',
        storageSize: '',
        // GPU
        serverId: '',
        gpuIndex: '',
        gpuCount: 1,
        resourceDescription: '',
        // 
        projectContributionScore: '',
        researchAchievementScore: '',
        resourceUsageEfficiencyScore: '',
        fairnessScore: '',
        contactPerson: '',
        contactPhone: '',
        contactEmail: ''
      }
    },
      
    // 
    onServerChange() {
      // 
      this.formData.gpuIndex = ''
      this.formData.gpuCount = 1
    },
    
    // 
    calculateResourceMatch(requested, available) {
      return requested <= available ? 1 : available / requested
    }
  },
  computed: {
      // 
      selectedServer() {
        return this.availableServers.find(server => server.id === parseInt(this.formData.serverId))
      },
      
      // 
      comprehensiveScore() {
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        const fairnessScore = parseInt(this.formData.fairnessScore)
        
        // 
        if (isNaN(projectContributionScore) || isNaN(researchAchievementScore) || isNaN(resourceUsageEfficiencyScore) || isNaN(fairnessScore)) {
          return 0
        }
        
        // (35%) + (25%) + (25%) + (15%)
        return projectContributionScore * 0.35 + researchAchievementScore * 0.25 + 
               resourceUsageEfficiencyScore * 0.25 + fairnessScore * 0.15
      },
      
      // 
      basicMatchScore() {
        if (!this.selectedServer) return 0;
        
        // 
        // formDatacpugpuCount
        const gpuMatch = this.calculateResourceMatch(
          parseInt(this.formData.gpuCount), 
          this.selectedServer.gpuCount
        );
        
        // 0-10
        return gpuMatch * 10;
      },
      
      // 
      availableGpuCount() {
        if (!this.selectedServer) {
          return 0
        }
        return this.selectedServer.availableGpus.length
      }
  },
  watch: {
    // 
    'formData.serverId': {
      handler() {
        this.onServerChange()
      }
    }
  }
}
</script>

