// 
import { createRouter, createWebHistory } from 'vue-router'

// 
import Login from '../views/Auth/Login.vue'
import Register from '../views/Auth/Register.vue'
import ResetPassword from '../views/Auth/ResetPassword.vue'

// Dashboard
import Dashboard from '../views/Dashboard.vue'

// 
import AdminRequests from '../views/Admin/AdminRequests.vue'
import ResourceMonitoring from '../views/Admin/ResourceMonitoring.vue'
import ResourceRequest from '../views/Admin/ResourceRequest.vue'
import ServerManagement from '../views/Admin/ServerManagement.vue'

// 
const routes = [
  // 
  { path: '/', redirect: '/login' },
  
  // 
  { path: '/login', component: Login },
  { path: '/register', component: Register },
  { path: '/reset-password', component: ResetPassword },
  
  // 
  { path: '/dashboard', component: Dashboard },
  
  // 
  { path: '/admin/requests', component: AdminRequests },
  { path: '/admin/resource-monitoring', component: ResourceMonitoring },
  { path: '/admin/resource-request', component: ResourceRequest },
  { path: '/admin/server-management', component: ServerManagement }
]

// 
const router = createRouter({
  history: createWebHistory(),
  routes
})

// 
export default router
