<template>
  <div class="my-requests-container">
    <!--  -->
    <div class="search-filter">
      <div class="search-input-wrapper">
        <input 
          v-model="searchKeyword" 
          type="text" 
          class="search-input" 
          placeholder="ID..." 
        >
        <span class="search-icon"></span>
      </div>
      <div class="filter-wrapper">
        <select v-model="statusFilter" class="filter-select">
          <option value=""></option>
          <option value="pending"></option>
          <option value="approved"></option>
          <option value="rejected"></option>
          <option value="processing"></option>
          <option value="completed"></option>
        </select>
        <select v-model="typeFilter" class="filter-select" v-if="!isAdmin">
          <option value=""></option>
          <option value="server"></option>
          <option value="gpu"></option>
          <option value="storage"></option>
          <option value="network"></option>
          <option value="software"></option>
          <option value="other"></option>
        </select>
      </div>
    </div>

    <!--  -->
    <div class="request-list">
      <div v-for="request in paginatedRequests" :key="request.id" class="request-item">
        <div class="request-header">
          <div class="request-title">
            <h4>{{ request.title }}</h4>
            <div class="request-id">ID: {{ request.id }}</div>
          </div>
          <div :class="['request-status', request.status]">{{ getStatusText(request.status) }}</div>
        </div>
        
        <div class="request-content">
          <div class="request-info">
            <div class="info-item">
              <span class="info-label">:</span>
              <span class="info-value">{{ request.applyTime }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">:</span>
              <span class="info-value">{{ getResourceTypeText(request.resourceType) }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">:</span>
              <span class="info-value">{{ request.expectedDate }}</span>
            </div>
            <div v-if="isAdmin && request.applicant" class="info-item">
              <span class="info-label">:</span>
              <span class="info-value">{{ request.applicant }}</span>
            </div>
            <div v-if="isAdmin && request.score" class="info-item">
              <span class="info-label">:</span>
              <span class="info-value">{{ request.score }}</span>
            </div>
            <div v-if="isAdmin && request.allocatedServer" class="info-item">
              <span class="info-label">:</span>
              <span class="info-value">{{ request.allocatedServer.name }}</span>
            </div>
          </div>
          
          <div class="request-description">
            {{ request.description }}
          </div>
        </div>
        
        <div class="request-actions">
          <button 
            class="view-button" 
            @click="handleViewDetail(request.id)"
          >
            
          </button>
          <button 
            class="view-button" 
            @click="handleCancelRequest(request.id)"
            v-if="!isAdmin && request.status === 'pending'"
          >
            
          </button>
          <button 
            class="view-button" 
            @click="handleAllocateServer(request.id)"
            v-if="isAdmin && request.status === 'pending'"
          >
            
          </button>
          <button 
            class="view-button" 
            @click="handleRejectRequest(request.id)"
            v-if="isAdmin && request.status === 'pending'"
          >
            
          </button>
        </div>
      </div>
      
      <!--  -->
      <div v-if="filteredRequests.length === 0" class="empty-state">
        <p></p>
      </div>
    </div>

    <!--  -->
    <div class="pagination" v-if="filteredRequests.length > 0">
      <div class="page-info">
         {{ filteredRequests.length }}  {{ pageSize }} 
      </div>
      <div class="page-controls">
        <button 
          class="page-btn" 
          @click="changePage(currentPage - 1)" 
          :disabled="currentPage === 1"
        >
          
        </button>
        <span class="page-num">
          {{ currentPage }} / {{ totalPages }}
        </span>
        <button 
          class="page-btn" 
          @click="changePage(currentPage + 1)" 
          :disabled="currentPage === totalPages"
        >
          
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RequestList',
  props: {
    requests: {
      type: Array,
      default: () => []
    },
    isAdmin: {
      type: Boolean,
      default: false
    },
    availableServers: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      searchKeyword: '',
      statusFilter: '',
      typeFilter: '',
      currentPage: 1,
      pageSize: 10
    }
  },
  methods: {
    // 
    handleViewDetail(requestId) {
      console.log('', requestId)
      // 
      this.$emit('view-detail', requestId)
    },
    
    // 
    handleCancelRequest(requestId) {
      if (confirm('')) {
        console.log('', requestId)
        // API
        this.$emit('cancel-request', requestId)
        alert('')
      }
    },
    
    // 
    handleAllocateServer(requestId) {
      this.$emit('allocate-server', requestId)
    },
    
    // 
    handleRejectRequest(requestId) {
      if (confirm('')) {
        this.$emit('reject-request', requestId)
        alert('')
      }
    },
    
    // 
    changePage(pageNum) {
      if (pageNum > 0 && pageNum <= this.totalPages) {
        this.currentPage = pageNum
      }
    },
    
    // 
    getStatusText(status) {
      const statusMap = {
        pending: '',
        approved: '',
        rejected: '',
        processing: '',
        completed: ''
      }
      return statusMap[status] || status
    },
    
    // 
    getResourceTypeText(type) {
      const typeMap = {
        server: '',
        storage: '',
        network: '',
        software: '',
        other: '',
        gpu: ''
      }
      return typeMap[type] || type
    },
    
    // 
    filterRequests(requests) {
      return requests.filter(request => {
        // 
        const matchesStatus = !this.statusFilter || request.status === this.statusFilter;
        // 
        const matchesType = !this.typeFilter || request.resourceType === this.typeFilter;
        // 
        const matchesSearch = !this.searchKeyword || 
          request.title?.includes(this.searchKeyword) || 
          request.id?.includes(this.searchKeyword) ||
          (this.isAdmin && request.applicant?.includes(this.searchKeyword));
        return matchesSearch && matchesStatus && (!this.isAdmin || matchesType);
      });
    }
  },
  computed: {
    // 
    filteredRequests() {
      return this.filterRequests(this.requests)
    },
    
    // 
    paginatedRequests() {
      const startIndex = (this.currentPage - 1) * this.pageSize
      const endIndex = startIndex + this.pageSize
      return this.filteredRequests.slice(startIndex, endIndex)
    },
    
    // 
    totalPages() {
      return Math.ceil(this.filteredRequests.length / this.pageSize)
    }
  },
  watch: {
    // 
    searchKeyword() {
      this.currentPage = 1
    },
    statusFilter() {
      this.currentPage = 1
    },
    typeFilter() {
      this.currentPage = 1
    }
  }
}
</script>

